import React, { useState } from 'react';
import Expense from './Componets/Expense/Expense';
import NewExpense from './Componets/NewExpense/NewExpense';
const DUMMY_EXPENSES = [
	{
		id: 'e1',
		title: 'Toilet Paper',
		amount: 94.12,
		date: new Date(2020, 7, 14),
	},
	{ id: 'e2', title: 'New TV', amount: 799.49, date: new Date(2021, 2, 12) },
	{
		id: 'e3',
		title: 'Car Insurance',
		amount: 294.67,
		date: new Date(2021, 2, 28),
	},
	{
		id: 'e4',
		title: 'New Desk (Wooden)',
		amount: 450,
		date: new Date(2021, 5, 12),
	},
];
const App = () => {
	const [dummyExpenses, setDummyExpenses] = useState(DUMMY_EXPENSES);
	const newExpenseDataHandler = (expense) => {
		setDummyExpenses((previousExpense) => {
			return [expense, ...previousExpense];
		});
	};
	return (
		<div>
			<NewExpense OnNewExpense={newExpenseDataHandler} />
			<Expense expenses={dummyExpenses} />
		</div>
	);
};

export default App;
